import { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Send } from "lucide-react";

export default function ChatBot() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const [chatId, setChatId] = useState(null);
  const startNewChat = async () => {
    try {
      const response = await fetch("http://localhost:8000/start_chat", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify({ title: "Prev Chat" }) // Add title to request
      });
  
      const data = await response.json();
      setChatId(data.chat_id);
    } catch (error) {
      console.error("Error starting chat:", error);
    }
  };
  
  useEffect(() => {
    startNewChat(); // Start a new chat when component mounts
  }, []);  

  const sendMessage = async () => {
    if (!input.trim()) return;
    
    const timestamp = new Date().toISOString();
    const newMessage = { sender: "user", text: input, timestamp };

    setMessages((prev) => [...prev, newMessage]);
    setInput("");

    try {
      const response = await fetch("http://localhost:8000/save_chat", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify({
          chat_id: chatId,
          message: newMessage, 
        }),
      });

      const data = await response.json();
      console.log("Chat saved:", data);
    } catch (error) {
      console.error("Error sending message:", error);
    }
  };
  const endChat = async (chatId, chatTitle) => {
    console.log("chatId:", chatId, "chatTitle:", chatTitle, "Type of chatId:", typeof chatId);
  
    if (chatId instanceof Event) {
      console.error("âŒ Event object mistakenly passed to endChat! Aborting.");
      return;
    }
  
    try {
      const response = await fetch("http://localhost:8000/end_chat", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify({ chat_id: String(chatId), title: String(chatTitle) }), // ðŸ”¹ Convert to string
      });
  
      const data = await response.json();
      if (data.error) {
        console.error("Error ending chat:", data.error);
        return;
      }
  
      // âœ… Update state
      setChats((prevChats) => [...prevChats, { chat_id: chatId, title: chatTitle, messages: [] }]);
  
    } catch (error) {
      console.error("Error ending chat:", error);
    }
  };
  
  
  
  return (
    <div className="flex flex-col h-screen mx-auto p-4 bg-gray-900 text-white rounded-lg shadow-lg border border-gray-700 custom-scrollbar">
      <h1 className="text-2xl font-semibold text-center mb-4 text-gray-200">
        AI-Powered Assistant
      </h1>
      <div className="flex-1 overflow-y-auto space-y-3 p-2 bg-gray-800 rounded-lg shadow-inner custom-scrollbar">
        {messages.map((msg, idx) => (
          <Card
            key={idx}
            className={`rounded-2xl p-3 shadow-md text-sm max-w-xs ${
              msg.sender === "user"
                ? "bg-gray-500 text-white"
                : "bg-gray-700 text-gray-300"
            }`}
          >
            <CardContent>
              <p className="text-xs text-gray-400">
                {new Date(msg.timestamp).toLocaleTimeString()}
              </p>
              <p>{msg.text}</p>
            </CardContent>
          </Card>
        ))}
      </div>
      <div className="flex gap-3 mt-4 bg-gray-800 p-3 rounded-lg shadow-md">
        <Input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message..."
          className="flex-1 bg-gray-700 text-white border-none focus:ring-2 focus:ring-blue-400 rounded-lg p-2"
        />
        <Button
          onClick={sendMessage}
          className="p-3 bg-blue-500 hover:bg-blue-600 text-white rounded-lg shadow-md"
        >
          <Send size={20} />
        </Button>
        <Button
          onClick={endChat}
          className="p-3 bg-blue-500 hover:bg-blue-600 text-white rounded-lg shadow-md"
        >
          Save Chat
        </Button>
      </div>
    </div>
  );
}
