const Sidebar = ({ chats, setChats, onSelectChat }) => {
  useEffect(() => {
    const fetchChats = async () => {
      try {
        const response = await fetch("http://localhost:8000/chats", {
          method: "GET",
          headers: { "Content-Type": "application/json" },
          credentials: "include",
        });
        const data = await response.json();
        setChats(data); // âœ… Update lifted state
      } catch (error) {
        console.error("Error fetching chats:", error);
      }
    };

    fetchChats();
    const interval = setInterval(fetchChats, 5000);
    return () => clearInterval(interval);
  }, [setChats]);

  return (
    <div className="w-full h-screen bg-gray-900 text-white p-6 overflow-y-auto">
      <ul>
        {chats.map((chat, index) => (
          <li key={chat.id || index} onClick={() => onSelectChat(chat)}>
            <strong>{chat.title || "Untitled Chat"}</strong>
          </li>
        ))}
      </ul>
    </div>
  );
};
